import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import ProjectCard from "./ProjectCards";


import figo from "../../Assets/Projects/figo.JPG";
import quiz from "../../Assets/Projects/quiz.JPG";
import todo from "../../Assets/Projects/todo.JPG";
import weather from "../../Assets/Projects/weather.JPG";

function Projects() {
  return (
    <Container fluid className="project-section">
  
      <Container>
        <h1 className="project-heading">
          My Recent <strong className="purple">Works </strong>
        </h1>
        <p style={{ color: "white" }}>
          Here are a few projects I've worked on recently.
        </p>
        <Row style={{ justifyContent: "center", paddingBottom: "10px" }}>
          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={weather}
              isBlog={false}
              title="Weather App"
              description="Weather App You can check live weather of any city in te world."
              link="https://github.com/fayyazcode/reactWeatherApp"
              links="https://reactweatherrapp.netlify.app/"
            />
          </Col>

          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={todo}
              isBlog={false}
              title="Todo App "
              description="Todo App on Javascript HTML and Css "
              link="https://github.com/fayyazcode/toDoList"
              links="https://todoapp-fayyaz.netlify.app/"
            />
          </Col>

          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={figo}
              isBlog={false}
              title="Custom Templet "
              description="Templet of Figo website full responsive technologies used  HTML Bootstrap and CSS"
              link="https://github.com/fayyazcode/figo-By-Fayyaz"
              links="https://figo-saylani.netlify.app/"
            />
          </Col>

          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={quiz}
              isBlog={false}
              title="Quiz App"
              description="Quiz app using HTML CSS Bootstrap and Javascript"
              link="https://github.com/fayyazcode/quizApp"
              links="https://quiz-ap.netlify.app"
            />
          </Col>

          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={todo}
              isBlog={false}
              title="Todo App "
              description="Todo App on React  "
              link="https://github.com/fayyazcode/react-todo"
              links="https://react-todo-fayyaz.netlify.app/"
            />
          </Col>

          
  
        </Row>
      </Container>
    </Container>
  );
}

export default Projects;
